body {
  margin: 0;
  padding: 0;
  background-color: #D6D8D5;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-sections {
  background-color: #D6D8D5;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.App-section1 {
  display: flex;
  align-self: flex-end;
  height: 90vmin;
}

.App-section2 {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  padding: 0 5vmin 0 5vmin;
}

.App-section2 .header {
  display: flex;
  font-size: calc(10px + 6vmin);
  color: black;
}

.App-section2 .text {
  display: flex;
  align-self: initial;
  font-size: calc(5px + 1.5vmin);
  color: black;
}
